@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed/RobotoCondensed-Bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTMono/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono/PTMono-Regular.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  font-size: 16px;
  line-height: 1.5; }

body {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  min-width: 320px;
  min-height: 100%;
  color: #666666;
  font-family: "PT Mono", "Georgia", sans; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

.page-wrapp {
  flex-grow: 1; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%); }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto Condensed", "Trebuchet MS", sans-serif; }

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 35px; }
  .container--big {
    max-width: 1480px; }

@media screen and (max-width: 1023px) {
  .container {
    padding: 0 25px; } }

@media screen and (max-width: 767px) {
  .container {
    padding: 0 15px; } }

.button {
  display: inline-block;
  padding: 14px 24px;
  color: #00b2ff;
  font-family: "Roboto Condensed", "Trebuchet MS", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  border: 1px solid #00b2ff;
  border-radius: 5px; }

.button--white {
  color: #ffffff;
  border: 1px solid #ffffff; }

.button--fill {
  color: #ffffff;
  background-color: #00b2ff;
  border: none; }

.logo {
  display: block;
  width: 202px;
  height: 52px; }

.logo img {
  width: 100%;
  height: 100%; }

@media screen and (max-width: 767px) {
  .logo {
    width: 38px;
    height: 38px; } }

.nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.nav li {
  margin-right: 88px; }
  .nav li:last-child {
    margin-right: 0; }

.nav a {
  display: flex;
  align-items: center;
  color: #ffffff;
  text-decoration: none; }

.nav svg {
  margin-right: 15px; }

@media screen and (max-width: 1023px) {
  .nav span {
    display: none; }
  .nav li {
    margin-right: 33px; }
  .nav svg {
    width: 24px;
    height: 24px;
    margin: 0; } }

.page-header {
  position: absolute;
  display: none;
  width: 100%;
  padding: 33px 0; }

.page-header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.page-header__nav {
  margin-left: auto; }

.page-header__call {
  margin-left: 50px; }

@media screen and (max-width: 1023px) {
  .page-header__call {
    display: none; } }

.page-promo {
  display: none;
  padding: 320px 0 275px;
  overflow: hidden;
  color: #ffffff;
  background: #1b2d37 url("../img/desktop/header-bg-desktop@1x.png") no-repeat;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-promo {
      background-image: url("../img/desktop/header-bg-desktop@2x.png"); } }

.page-promo__container {
  position: relative; }

.page-promo__content {
  position: relative;
  z-index: 2;
  width: 55%; }

.page-promo__title {
  margin: 0 0 40px;
  font-weight: bold;
  font-size: 80px;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 5px; }

.page-promo__desc {
  position: relative;
  margin-bottom: 42px;
  padding-left: 95px;
  font-size: 22px;
  line-height: 1.5; }
  .page-promo__desc::before {
    position: absolute;
    left: 0;
    top: 15px;
    width: 66px;
    height: 2px;
    background-color: #ffffff;
    content: ""; }

.page-promo__desc p {
  margin: 0; }
  .page-promo__desc p + p {
    margin-top: 15px; }

.page-promo__advice {
  margin-left: 95px;
  padding: 20px 41px;
  font-size: 16px; }
  .page-promo__advice--mobile {
    display: none; }

.page-promo__img {
  position: absolute;
  top: -62px;
  right: -45px; }
  .page-promo__img::before {
    position: absolute;
    top: 13px;
    right: 78px;
    width: 635px;
    height: 637px;
    background: url("../img/triangles.svg") no-repeat;
    background-size: 630px auto;
    content: ""; }

.page-promo__img img {
  position: relative;
  z-index: 1;
  width: 705px;
  height: 515px; }

@media screen and (max-width: 1023px) {
  .page-promo {
    padding: 204px 0 163px;
    background-position: center; }
  .page-promo__content {
    width: 70%;
    padding-left: 64px; }
  .page-promo__title {
    margin: 0 0 32px;
    font-size: 60px;
    line-height: 68px; }
  .page-promo__desc {
    margin-bottom: 54px;
    padding-left: 50px;
    font-size: 18px; }
    .page-promo__desc::before {
      width: 33px; }
  .page-promo__advice {
    margin-left: 50px;
    padding: 20px 29px; }
  .page-promo__img {
    top: -78px;
    right: -207px; }
    .page-promo__img::before {
      top: -31px;
      right: 75px;
      width: 460px;
      height: 384px;
      background: url("../img/triangles.svg") no-repeat;
