.page-feature {
  display: none;
  margin: 110px 0; }

.page-feature__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.page-feature__item {
  width: 48.72%;
  min-height: 301px;
  margin-right: 2.5%;
